.Mainform{
    background-color: rgb(0, 0, 0); 
    padding: 20px;
    width: 50%;
    color: whitesmoke; 
    min-width: 300px;
}
 

.labelDiv{  
    display: flex;   
    justify-content: space-between;
    padding: 10px;
    margin: 2px;
}
 
.labelDiv2{
    /* min-width: 50px; */
    display: flex  ;
    border: 1px solid white ; 
    width: 20%;
    min-width: 80px;
} 

span{
    width: 25px;
}
.button{
    background-color: rgb(0, 0, 0);
    padding: 10px;
    margin-left:45% ; 
    color: white;
    border: whitesmoke solid 2px;

}

.finalAmount{
    /* color: yellow; */ 
    display: flex; 
    justify-content: center;
}


.slider {
    -webkit-appearance: none;
    width: 100%;
    height: auto;
    border-radius: 5px;  
    /* background: #e7e0e0; */
    outline: none;
    opacity: 0.5;
    -webkit-transition: .2s;
    transition: opacity .2s; 
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 100%;
    height: 25px;
    border-radius: 50%; 
    background: #04AA6D;
    cursor: pointer;
  }
  
.slider::-moz-range-thumb {
    width: 100%;
    height: 25px;
    border-radius: 50%;
    background: #04AA6D;
    cursor: pointer;
}

.range{
    width: 90%;
}

.labelPrint{
    padding-top: 10px;
    display: flex;
    justify-content: space-evenly; 
    height: 5vh;
    margin: 5px;
}
.labelPrint p{ 
    height: 5vh;
    margin: 0px;
    width: 20%;
    min-width: 80px;
}
.labelPrint label{
    height: 5vh;
    margin: 0px;
    width: 30%;
    min-width: 160px;
    display: inherit;
    justify-content: center;
}

.sLbtn{
    background-color: white;
    font-weight: bolder; 
    border-radius: 12%;
    padding: 10px;
}
.sLbtn:hover{
    background-color: red;
    color: white;
    cursor: pointer;
}

.sLbtn button.active, .sLbtnActive{
    background-color: red;
    color: white;

}